.navbar{
    height: 100px;
   position: fixed;
   top: 0;
   display: flex;
   justify-content: space-between;
   align-items: center;
    z-index: 44;
    width: 100%;
    background-color: rgba(17, 17, 17, 0.9);
    backdrop-filter: blur(5px);
    padding: 0 50px;
    box-sizing: border-box;
}
.navbar .logo {
    width: 100px;
}
.navbar .logo img{
    width: 100%;
    display: block;
}
.navbar .avatar {
    width: 40px;
    cursor: pointer;
}
.navbar .avatar img{
    width: 100%;
    display: block;
    border-radius: 8px;
}
