.banner {
    background-size: cover;
    background-position: top;
    height: 800px;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 3;
    box-sizing: border-box;
}
.fade {
    background: linear-gradient(0deg, rgba(2,0,36,1) 0%, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 5%, rgba(0,212,255,0) 100%);
    display: block;
    height: 800px;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    
}
.banner .content {
  padding: 0 100px;
  color: #fff;
}
.banner .content h1 {
  font-size: 3rem;
  font-weight: 800;
  width: 70%;
}
.banner .content .buttons {
  display: flex;
  align-items: center;
}
.banner .content .buttons button {
  border: none;
  margin-right: 16px;
  cursor: pointer;
  outline: none;
  font-weight: 700;
  border-radius: 6px;
  color: #fff;
  padding: 12px 2rem;
  background: rgba(51, 51, 51, 0.5);
  transition: 0.4s ease;
}
.banner .content .buttons button:hover {
  color: #000;
  background-color: #e6e6e6;
}
.banner .content p {
  width: 60%;
  line-height: 1.3em;
  font-size: 1rem;
  max-width: 860px;
}
body {
    background-color: #111;
}