.row {
  padding-left: 20px;
  color: #fff;
}
.row .posters {
  display: flex;
  padding: 20px;
  overflow-x: scroll;
  overflow-y: hidden;
}
.row .posters::-webkit-scrollbar {
  display: none;
}
.row .posters .item {
  width: 600px;
}

.row .posters .box img {
  max-height: 250px;
  transition: 0.3s ease;
  border-radius: 10px;
}
.row .posters .box {
    position: relative;
    cursor: pointer;
     margin-right: 20px;
     transition: .3s ease;
}
.row .posters .box .overview {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    top: -100%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.6);
    transition: .4s ease;
    border-radius: 10px;
    opacity: 0;
}
.row .posters .box:hover{
    transform: scale(1.03);
}
.row .posters .box:hover .overview{
    top: 50%;
    opacity: 1;
}
.row .posters .box span {
    font-size: 14px;
    color: #fff;
    width: 60%;
    margin: 0 auto; 
}
.row .posters img.small-box {
  max-height: 150px;
}
.html5-video-player{
    margin-right: 16px;
}